<template>
  <!-- aside-group -->
  <div :class="sidebarClass" class="aside-group" id="aside_func">
    <!-- aside-group-item -->
    <div class="aside-group-item aside-group-history">
      <div class="aside-item-title">標籤紀錄</div>
      <div class="aside-item-detail">
        <ul v-if="history.length!==0" class="aside-history-div">
          <router-link class="aside-history-link" v-for="route in history" :key="route.id" :to="route.path">
            {{route.name}}
          </router-link>
        </ul>
        <ul v-else>
          <li class="tag-null">無瀏覽紀錄</li>
        </ul>
        <ul>
          <li>
            <span class="tag-link"><a href="#">提示訊息</a></span>
            <span class="tag-cancel"
              ><a href="#"><span class="material-icons">cancel</span></a></span
            >
          </li>
          <li>
            <span class="tag-link"><a href="#">提示訊息</a></span>
            <span class="tag-cancel"
              ><a href="#"><span class="material-icons">cancel</span></a></span
            >
          </li>
        </ul>
        <div class="history-cancel">
          <div class="history-cancel-inner">
            <a href="javascript:;">全部移除</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  props: {
    toggle: Boolean,
  },
  computed: {
    sidebarClass() {
      // console.log(this.toggle);
      let style = "aside-group";
      if (this.toggle) {
        style = style.concat(" aside-open");
      }
      return style;
    },
    history() {
      return this.$store.state.history;
    },
  },
};
</script>
