<template>
  <!-- search -->
  <div class="content-component content-search">
    <div class="search-inner">
      <div class="search-field">
        <div class="search-field-input">
          <div class="field-inner">
            <form @submit.prevent="queryFormSubmit">
              <div class="field-item field-select">
                <select v-model="searchCat">
                  <option v-for="(opt, i) in options" :key="i">{{
                    opt
                  }}</option>
                </select>
              </div>
              <div class="field-item field-input">
                <input
                  type="text"
                  placeholder="請輸入搜尋條件"
                  v-model="searchInput"
                  value="searchInput"
                />
              </div>
              <div class="field-item field-Btn">
                <button>搜 尋</button>
              </div>
            </form>
          </div>
        </div>
        <div class="search-field-result">
          <div class="field-result-inner">
            <div>搜尋條件</div>
            <div>{{ searchCat }}</div>
            <div>{{ searchInput }}</div>
            <div>
              共<span>{{ searchQuantity }}</span
              >筆
            </div>
            <div v-if="searchQuantity === 0">
              <span class="noDataText">，查無資料!</span>
            </div>
            <!-- <div class="resultToggleBtn"><a href="javascript:;" class="resultToggle-up"></a></div> -->
            <div v-b-toggle.collapse-3 class="resultToggleBtn">
              <a
                class="resultToggleIcon"
                :class="toggle"
                @click="searchToggle"
              ></a>
            </div>
          </div>
        </div>
      </div>
      <!-- table -->
      <b-collapse visible id="collapse-3">
        <div class="content-component content-table">
          <div class="table-func">
            <div class="table-filter">
              <div class="table-filter-title">篩選</div>
              <div class="table-filter-field">
                <input type="text" id="search-table-tableFilter" />
              </div>
            </div>

            <div class="table-switch">
              <div id="searchList" :class="visibility">
                <span class="anchor" @click="showAnchor">自定義欄位</span>
                <ul class="items" id="checkbox-items">
                  <Checkbox
                    :options="colOptions"
                    :checked="checkedCols"
                    @clicked="clickCheckbox"
                    id1="search-check1"
                    id2="search-check2"
                  />
                </ul>
              </div>
            </div>
          </div>
          <div id="search-table"></div>
        </div>
      </b-collapse>
    </div>
    <slot :queryData="queryData"></slot>
  </div>
</template>

<script>
import * as $ from "jquery";
import { createTable, showCols } from "../helperFunctions/testTable.js";
import { testData } from "../helperFunctions/testData.js";
import Checkbox from "./Checkbox.vue";

const tableColumns = [
  { title: "id", field: "id" },
  { title: "保單號碼", field: "a" },
  { title: "要保日", field: "b" },
  { title: "生效日", field: "c" },
  { title: "主約險種", field: "d" },
  { title: "狀態", field: "e" },
  { title: "參考用每", field: "f" },
  { title: "要保人", field: "g" },
  { title: "被保人", field: "h" },
  { title: "收費地區", field: "i" },
  { title: "應繳費日", field: "j" },
  { title: "送金到期日", field: "k" },
  { title: "繳法", field: "l" },
  { title: "收費方式", field: "m" },
  { title: "服務業務員", field: "n" },
  { title: "收費指示", field: "o" },
  { title: "住所指示", field: "p" },
  { title: "核定日期", field: "q" },
  { title: "應付未付指示", field: "r" },
];

const colOptions = tableColumns.map( tableColumn => (
  {
    text: tableColumn.title,
    value: tableColumn.field
  }
))

const checkedCols = tableColumns.map( tableColumn => {
  return tableColumn.field
})

export default {
  data() {
    return {
      queryData: null,
      tabledata: [],
      tableColumns: tableColumns,
      colOptions: colOptions,
      checkedCols: checkedCols, //array -> default should be all checked
      anchor: false,
      table: null,
      expanded: true,
      searchInput: "",
      searchCat: "保單號碼/ID",
      options: [
        "保單號碼/ID",
        "要保人ID",
        "被保人ID",
        "要/被保人姓名",
        "要保人姓名",
        "被保人姓名",
        "生存/滿期受益人ID",
        "生存/滿期受益人姓名",
      ],
    };
  },
  components: {
    Checkbox,
  },
  mounted() {
    const vm = this;
    this.table = createTable(
      "#search-table",
      this.tableColumns,
      this.tabledata,
      vm
    );
  },
  methods: {
    //搜尋
    queryFormSubmit() {
      this.tabledata = testData();
      //this.tabledata = [];
      this.queryData = { data: testData() };
      this.$store.commit("setQueryData", this.queryData);
      this.loadData();
    },
    loadData() {
      //將資料放入table中
      if (this.tabledata.length === 0) {
        //查無資料
      }
      this.table.setData(this.tabledata);
    },
    searchToggle() {
      this.expanded = !this.expanded;
    },
    showAnchor() {
      this.anchor = !this.anchor;
    },
    clickCheckbox(value) {
      this.checkedCols = value;
      showCols(this.checkedCols, this.colOptions, this.table);
    },
  },
  computed: {
    searchQuantity() {
      return this.tabledata.length;
    },
    toggle() {
      return {
        "resultToggle-up": this.expanded,
        "resultToggle-up resultToggle-down": !this.expanded,
      };
    },
    visibility() {
      let style = "table-switch-list";
      if (this.anchor) {
        style = style.concat(" visible");
      }
      return style;
    },
  },
};
</script>

<style lang="scss">
@import "~vue-tabulator/dist/scss/bootstrap/tabulator_bootstrap4";

.page-length {
  display: inline-block;
  padding: 0px 5px;
}

.page-length p {
  margin-bottom: 0px;
}

.page-num {
  display: inline-block;
  padding: 0px 5px;
}

.page-num p {
  margin-bottom: 0px;
}

.tabulator-footer {
  background-color: white;
}
</style>
