<template>
  <div class="content-component content-breadcrumb">
    <ul>
      <li><router-link to="/qrysys/homepage">保單價值/內容</router-link></li>
      <li><router-link to="/qrysys/homepage">保單價值</router-link></li>
      <li><router-link to="/qrysys/homepage">年金提存明細</router-link></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Breadcrumbs",
  data() {
    return {
      breadcrumbList: [],
    };
  },
  mounted() {
    this.updateList();
  },
  watch: {
    $route() {
      this.updateList();
    },
  },
  methods: {
    routeTo(pRouteTo) {
      if (this.breadcrumbList[pRouteTo].link) {
        this.$router.push(this.breadcrumbList[pRouteTo].link);
      }
    },
    updateList() {
      this.breadcrumbList = this.$route.meta.breadcrumb;
    },
  },
}
</script>